<template>
  <div>
    <van-image :fit="fit" :src="src" v-lazy="src" @click="view" :style="{width:width, height:height}">
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
      <template v-slot:error>{{errMsg}}</template>
    </van-image>
    <van-image-preview v-model="show" :showIndex="false" :images="images">
    </van-image-preview>
  </div>
</template>
<script>
/****
 * 统一图片组件，本组件支持单张图片预览
 * gfy
 * 2019-08-20
 *  */
import { ImagePreview } from "vant";
export default {
  name: "r-image",
  components: { [ImagePreview.Component.name]: ImagePreview.Component },
  props: {
    // maxWidth: {
    //   type: String,
    //   default: "10rem"
    // },
    // maxHeight: {
    //   type: String,
    //   default: "10rem"
    // },
    width: {
      type: String,
      default: "100px"
    },
    height: {
      type: String,
      default: "100px"
    },
    fit: {
      type: String,
      default: "scale-down"
    },
    src: {
      type: String,
      default: ""
    },
    errMsg: {
      type: String,
      default: "加载失败"
    }
  },
  data() {
    return {
      show: false,
      images: [this.src]
    };
  },
  methods: {
    view () {
      this.show = true;
      this.images = [this.src]
    }
  }
};
</script>



